import { Component, Input, OnDestroy, OnInit, inject } from '@angular/core';
import { FormBuilder, UntypedFormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfirmationService } from 'primeng/api';
import { Subscription } from 'rxjs';
import {
  LineaOrdenCompra,
  LineaOrdenCompraParaRecibir,
  OrdenCompra,
} from 'src/app/interfaces/orden-compra.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { OrdenesService } from 'src/app/services/ordenes.service';

@Component({
  selector: 'app-revisar-cantidad-linea-orden-compra',
  templateUrl: './revisar-cantidad-linea-orden-compra.component.html',
  styleUrls: ['./revisar-cantidad-linea-orden-compra.component.scss'],
})
export class RevisarCantidadLineaOrdenCompraComponent
  implements OnInit, OnDestroy
{
  private readonly Form = inject(FormBuilder);
  private readonly ordenesService = inject(OrdenesService);
  private readonly messageService = inject(CustomMessageService);
  private readonly router = inject(Router);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly confirmationService = inject(ConfirmationService);

  public subs: Subscription[] = [];

  lineasOrdenCompra!: LineaOrdenCompraParaRecibir[];

  public lineaOrdenCompraSeleccionada: LineaOrdenCompraParaRecibir = null;

  public ordenID: number = null;
  public ubicacionId: number = null;

  public hadSubmit: boolean = false;

  public loadingView: boolean = true;

  public confirmarLineaOrdenCompraRQBody = this.Form.group({
    idOrdenCompraLinea: [null],
    idUbicacion: [null, Validators.required],
    codigoEan: [null, Validators.required],
    cantidad: [null, Validators.required],
  });
  get idOrdenCompraLinea() {
    return this.confirmarLineaOrdenCompraRQBody.get(
      'idOrdenCompraLinea'
    ) as UntypedFormControl;
  }
  get idUbicacion() {
    return this.confirmarLineaOrdenCompraRQBody.get(
      'idUbicacion'
    ) as UntypedFormControl;
  }
  get codigoEan() {
    return this.confirmarLineaOrdenCompraRQBody.get(
      'codigoEan'
    ) as UntypedFormControl;
  }
  get cantidad() {
    return this.confirmarLineaOrdenCompraRQBody.get(
      'cantidad'
    ) as UntypedFormControl;
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe({
      next: async (params) => {
        const requestJson = {
          ubicacion: await params['ubiId'],
          ordenCompra: await params['idOrden'],
        };
        this.ordenID = await params['idOrden'];
        this.ubicacionId = await params['ubiId'];
        this.recibirCamionDeOrdenCompraRQ(requestJson);
      },
    });

    // setTimeout(() => {
    //   console.log('LAS LINEAS CON LAS QUE VAMOS A TRABAJAR =>', this.lineasOrdenCompra);
    // }, 2000);
  }

  back() {
    this.router.navigateByUrl('/almacen/confirmar-orden-compra');
  }

  validar(campo) {
    return this.confirmarLineaOrdenCompraRQBody.get(campo).valid;
  }

  esEntero(num: number) {
    if (num === null) return true;
    if (num === 0) return true;
    if (Number.isInteger(num)) {
      return true;
    } else return false;
  }

  esPositivo(num: number) {
    if (num < 0) {
      return false;
    }
    return true;
  }

  navegarFinalizarOrden() {
    sessionStorage.setItem(
      'lastUrl',
      `/almacen/reivisar-cantidad-linea-orden-compra/${this.ordenID}/${this.ubicacionId}`
    );
    this.router.navigateByUrl(
      '/almacen/finalizar-orden-compra/' + this.ordenID
    );
  }

  cargarLineaOrdenCompra(barcode: number) {
    this.lineaOrdenCompraSeleccionada = null;
    this.lineasOrdenCompra.forEach((linea) => {
      if (linea.codigoEan == barcode) {
        this.lineaOrdenCompraSeleccionada = linea;
        this.confirmarLineaOrdenCompraRQBody.reset(
          this.lineaOrdenCompraSeleccionada as any
        );
      }
    });
    if (this.lineaOrdenCompraSeleccionada == null) {
      const regex = /^[0-9]*$/;
      const barcodeString = barcode.toString();
      if (regex.test(barcodeString) && barcodeString.length == 13) {
        this.codigoEan.setValue(parseInt(barcodeString));
        this.idUbicacion.setValue(this.ubicacionId);
        this.idOrdenCompraLinea.setValue(null);
        this.cantidad.setValue(null);
      }
    }
    console.log(this.confirmarLineaOrdenCompraRQBody.value);
  }

  submit() {
    this.hadSubmit = true;

    const formValue = this.confirmarLineaOrdenCompraRQBody.value;

    console.log('el form => ', formValue);

    if (
      this.confirmarLineaOrdenCompraRQBody.invalid ||
      !this.esEntero(formValue.cantidad) ||
      !this.esPositivo(formValue.cantidad)
    ) {
      this.messageService.error('Verifique los campos');
      return;
    }

    this.loadingView = true; //cargando

    const requestJson = JSON.parse(JSON.stringify(formValue));

    this.cofirmarCantidadLineaOrdenCompra(requestJson);
  }

  cofirmarCantidadLineaOrdenCompra(requestJson) {
    console.log('LAS LINEAS ESTABAN ASI => ', this.lineasOrdenCompra);
    this.subs.push(
      this.ordenesService
        .moverMercanciaUbicacionEntrada(requestJson)
        .subscribe({
          next: (res) => {
            console.log('LINEA CONFIRMADA => ', res);
            this.messageService.win('Cantidad confirmada');
            this.hadSubmit = false;
            this.loadingView = false; // dejar de cargar

            if (this.lineaOrdenCompraSeleccionada != null) {
              this.lineaOrdenCompraSeleccionada.cantidad =
                this.lineaOrdenCompraSeleccionada.cantidad -
                this.cantidad.value;

              console.log(
                'SU CANTIDAD QUEDO EN => ',
                this.lineaOrdenCompraSeleccionada.cantidad
              );

              if (this.lineaOrdenCompraSeleccionada.cantidad < 1) {
                this.lineasOrdenCompra = this.lineasOrdenCompra.filter(
                  (linea) => {
                    return (
                      linea.idOrdenCompraLinea !=
                      this.lineaOrdenCompraSeleccionada.idOrdenCompraLinea
                    );
                  }
                );
              }

              console.log(
                'LAS LINEAS QUEDARON ASI => ',
                this.lineasOrdenCompra
              );

              this.lineaOrdenCompraSeleccionada = null;
            }
            this.confirmarLineaOrdenCompraRQBody.reset();
          },
          error: (error) => {
            console.error(error);
            this.loadingView = false; // dejar de cargar
            error.error?.datail
              ? this.messageService.error(error.error.detail)
              : this.messageService.error(
                  'Ocurrio un error desconocido en local'
                );
          },
        })
    );
  }

  recibirCamionDeOrdenCompraRQ(requestJson) {
    this.subs.push(
      this.ordenesService.ordenCompraRecibirCamion(requestJson).subscribe({
        next: (res) => {
          console.log('LINEAS RECIBIDAS => ', res);
          this.lineasOrdenCompra = res;
          this.hadSubmit = false;
          this.loadingView = false;
        },
        error: (error) => {
          console.error(error);
          this.loadingView = false; // dejar de cargar
          error.error?.datail
            ? this.messageService.error(error.error.detail)
            : this.messageService.error(
                'Ocurrio un error desconocido en local'
              );
          this.router.navigateByUrl('/almacen/confirmar-orden-compra');
        },
      })
    );
  }

  ngOnDestroy(): void {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
