import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { FormBuilder, UntypedFormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';
import {
  LineaOrdenCompra,
  LineaOrdenCompraParaRecibir,
  OrdenCompra,
} from 'src/app/interfaces/orden-compra.interface';
import { Producto } from 'src/app/interfaces/producto.interface';
import { Proveedor } from 'src/app/interfaces/proveedor.interface';
import { Ubicacion } from 'src/app/interfaces/ubicaciones.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { FechaService } from 'src/app/services/fecha.service';
import { OrdenesService } from 'src/app/services/ordenes.service';
import { ProveedoresService } from 'src/app/services/proveedores.service';
import { UbicacionesService } from 'src/app/services/ubicaciones.service';

@Component({
  selector: 'app-confirmar-orden-compra',
  templateUrl: './confirmar-orden-compra.component.html',
  styleUrls: ['./confirmar-orden-compra.component.scss'],
})
export class ConfirmarOrdenCompraComponent implements OnInit, OnDestroy {
  private readonly Form = inject(FormBuilder);
  private readonly ubicacionesService = inject(UbicacionesService);
  private readonly proveedoresService = inject(ProveedoresService);
  private readonly ordenesService = inject(OrdenesService);
  private readonly messageService = inject(CustomMessageService);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly router = inject(Router);
  private readonly fechaService = inject(FechaService);

  public subs: Subscription[] = [];

  public ordenesCompras: OrdenCompra[];
  public ubicaciones: Ubicacion[];
  public proveedores: Proveedor[];

  public hadSubmit: boolean = false;

  public loadingView: boolean = false;
  public loadingOrdenes: boolean = false;

  public getLineasOrdenCompraRQBody = this.Form.group({
    ubicacion: [null, Validators.required],
    proveedor: [null, Validators.required],
    ordenCompra: [null, Validators.required],
  });
  get ubicacion() {
    return this.getLineasOrdenCompraRQBody.get(
      'ubicacion'
    ) as UntypedFormControl;
  }
  get proveedor() {
    return this.getLineasOrdenCompraRQBody.get(
      'proveedor'
    ) as UntypedFormControl;
  }
  get ordenCompra() {
    return this.getLineasOrdenCompraRQBody.get(
      'ordenCompra'
    ) as UntypedFormControl;
  }

  ngOnInit(): void {
    this.cargarUbicaciones();
    this.cargarProveedores();
  }

  back() {
    this.router.navigateByUrl('/almacen');
  }

  validar(campo) {
    return this.getLineasOrdenCompraRQBody.get(campo).valid;
  }

  cargarUbicaciones() {
    this.subs.push(
      this.ubicacionesService.getUbicacionesDeEntrada().subscribe({
        next: (res) => {
          console.log('UBICACIONES => ', res);
          this.ubicaciones = res;
        },
        error: (error) => {
          console.error(error);
          this.messageService.error(error.error.datail);
        },
      })
    );
  }
  cargarProveedores() {
    this.subs.push(
      this.proveedoresService.getProveedores().subscribe({
        next: (res) => {
          console.log('PROVEEDORES => ', res);
          this.proveedores = res;
        },
        error: (error) => {
          console.error(error);
          this.messageService.error(error.error.datail);
        },
      })
    );
  }

  cargarOrdenesComprasDelProveedor() {
    console.log('VOY A BUSCAR AL PROVEEDOR => ', this.proveedor);
    this.loadingOrdenes = true;
    this.ordenesCompras = null;
    this.subs.push(
      this.ordenesService
        .getOrdenesComprasByProveedor(this.proveedor.value.id)
        .subscribe({
          next: (res) => {
            console.log('ORDENES DE COMPRAS => ', res);
            this.ordenesCompras = res;

            this.ordenesCompras.forEach((orden) => {
              const fecheDeLaOrden = orden.fechaEjecucion
                ? this.fechaService.formatFechaAbsoluta(orden.fechaEjecucion)
                : 'N/A';
              const ordenNumero = orden.numero ? orden.numero : 'N/A';
              //@ts-ignore
              orden.label = `${ordenNumero} - ${fecheDeLaOrden}`;
            });
          },
          error: (error) => {
            console.error(error);
            this.messageService.error(error.error.datail);
          },
        })
    );
  }

  submit() {
    this.hadSubmit = true;

    if (this.getLineasOrdenCompraRQBody.invalid) {
      this.messageService.error('Verifique los campos');
      return;
    }

    this.loadingView = true; //cargando

    this.router.navigateByUrl(
      `/almacen/reivisar-cantidad-linea-orden-compra/${this.ordenCompra.value}/${this.ubicacion.value}`
    );
  }

  ngOnDestroy(): void {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
