import { Component, inject } from '@angular/core';
import { Router } from '@angular/router';

// the scanner!
import { BarcodeFormat } from '@zxing/library';
import { CustomMessageService } from 'src/app/services/custom-message.service';

@Component({
  selector: 'app-barcode-scanner',
  templateUrl: './barcode-scanner.component.html',
  styleUrls: ['./barcode-scanner.component.scss'],
})
export class BarcodeScannerComponent {

  private readonly messageService = inject(CustomMessageService);
  private readonly router = inject(Router);

  scannerActivo: boolean = false;
  notFound: boolean = false;

  codigoEan: string = null;

  hadSubmit: boolean = false;
  allowedFormats = [
    BarcodeFormat.EAN_13,
    BarcodeFormat.EAN_8,
    BarcodeFormat.CODE_128,
    BarcodeFormat.UPC_A,
    BarcodeFormat.UPC_E,
    BarcodeFormat.UPC_EAN_EXTENSION,
    BarcodeFormat.DATA_MATRIX,
    BarcodeFormat.QR_CODE,
  ];

  enableScanner() {
    this.scannerActivo = true;
  }

  mostrarCodigo(codigo) {
    this.scannerActivo = false;
    
    this.codigoEan = codigo
  }
}
