import { HttpClient } from '@angular/common/http';
import { Injectable, inject } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { Proveedor, ProveedoresPaginados } from '../interfaces/proveedor.interface';

@Injectable({
  providedIn: 'root'
})
export class ProveedoresService {
  private readonly http = inject(HttpClient);

  private API_URL = environment.API_URL;

  getProveedores(): Observable<Proveedor[]> {
    const url = this.API_URL + `/proveedores`;
    return this.http.get<Proveedor[]>(url);
  }

  getProveedor(id: number): Observable<Proveedor> {
    const url = this.API_URL + `/proveedor/${id}`;
    return this.http.get<Proveedor>(url);
  }
  
  getProveedoresPaginados(url): Observable<ProveedoresPaginados> {
    return this.http.get<ProveedoresPaginados>(url);
  }

  createProveedor(newPresupeusto: Proveedor): Observable<Proveedor> {
    const url = this.API_URL + `/proveedor`;
    return this.http.post<Proveedor>(url, newPresupeusto);
  }

  editProveedor(newPresupeusto: Proveedor): Observable<Proveedor> {
    const url = this.API_URL + `/proveedor`;
    return this.http.put<Proveedor>(url, newPresupeusto);
  }

  deleteProveedor(id: number): Observable<Proveedor> {
    const url = this.API_URL + `/proveedor/${id}`;
    return this.http.delete<Proveedor>(url);
  }
}
