import { CommonModule } from '@angular/common';
import { Component, Input, OnDestroy, OnInit, inject } from '@angular/core';
import { FormBuilder, UntypedFormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ConfirmationService } from 'primeng/api';
import { Subscription } from 'rxjs';
import {
  LineaOrdenCompra,
  LineaOrdenCompraParaRecibir,
  OrdenCompra,
} from 'src/app/interfaces/orden-compra.interface';
import { CustomMessageService } from 'src/app/services/custom-message.service';
import { OrdenesService } from 'src/app/services/ordenes.service';
import { PrimeNgModule } from '../../shared/prime-ng/prime-ng.module';
import { UbicacionesService } from 'src/app/services/ubicaciones.service';
import { Ubicacion } from 'src/app/interfaces/ubicaciones.interface';
import { Usuario } from 'src/app/interfaces/login/login-respuesta.interface';

@Component({
  selector: 'app-finalizar-orden-compra',
  standalone: true,
  imports: [CommonModule, PrimeNgModule],
  templateUrl: './finalizar-orden-compra.component.html',
  styleUrls: ['./finalizar-orden-compra.component.scss'],
})
export class FinalizarOrdenCompraComponent implements OnInit, OnDestroy {
  private readonly Form = inject(FormBuilder);
  private readonly ordenesService = inject(OrdenesService);
  private readonly ubicacionesService = inject(UbicacionesService);
  private readonly messageService = inject(CustomMessageService);
  private readonly router = inject(Router);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly confirmationService = inject(ConfirmationService);

  public subs: Subscription[] = [];

  public ordenID: number = null;

  public personaID: number = null;

  public hadSubmit: boolean = false;

  public loadingView: boolean = false;

  public selectUbicaciones: Ubicacion[] = [];
  public ubicacionesToSend: Ubicacion[] = [];
  public ubiSeleccinada: Ubicacion = null;

  ngOnInit(): void {
    this.activatedRoute.params.subscribe({
      next: async (params) => {
        this.ordenID = await params['idOrden'];
        this.personaID = (JSON.parse(localStorage.getItem('usuario')) as Usuario).id
        this.cargarUbicaciones();
      },
    });
  }

  async back() {
    const lastUrl = await sessionStorage.getItem('lastUrl');
    this.router.navigateByUrl(lastUrl);
  }

  cargarUbicaciones() {
    this.subs.push(
      this.ubicacionesService.getUbicacionesDeEntrada().subscribe({
        next: (res) => {
          console.log('UBICACIONES => ', res);
          this.selectUbicaciones = res;
        },
        error: (error) => {
          console.error(error);
          this.messageService.error(error.error.datail);
        },
      })
    );
  }

  agregarUbicacion(ubicacionSelected) {
    this.ubicacionesToSend.push(ubicacionSelected);
    
    setTimeout(() => {
      this.ubiSeleccinada = null;
      this.selectUbicaciones = this.selectUbicaciones.filter(ubicacion => {
        return ubicacion != ubicacionSelected
      })
    }, 100);
  }

  eliminarUbicacion(ubicacionId) {
    this.ubicacionesToSend = this.ubicacionesToSend.filter((ubicacion) => {
      if (ubicacion.ubicacionId == ubicacionId) {
        this.selectUbicaciones = [ubicacion].concat(this.selectUbicaciones)
      }
      return ubicacion.ubicacionId != ubicacionId;
    });
  }

  confirmarOrden() {
    this.hadSubmit = true;

    if (
      !this.ubicacionesToSend ||
      !this.ordenID ||
      this.ubicacionesToSend.length == 0 
    ) {
      this.messageService.error('Verifique los campos');
      return;
    }

    let ubicacionesToSendFinal = [];

    this.ubicacionesToSend.forEach(ubicacion => {
      ubicacionesToSendFinal.push(ubicacion.ubicacionId)
    })

    const requestJson = {
      id: this.ordenID,
      rrhh: this.personaID,
      ubicaciones: ubicacionesToSendFinal
    }

    this.confirmationService.confirm({
      key: 'confirmarOrden',
      accept: () => {
        this.confirmationService.close();
        this.ordenesService
          .confirmarOrdenRecibida(requestJson)
          .subscribe({
            next: (res) => {
              this.messageService.win('Orden de compra confirmada');
              this.router.navigateByUrl('/almacen/confirmar-orden-compra');
            },
            error: (error) => {
              console.error(error);
              error.error?.datail
                ? this.messageService.error(error.error.detail)
                : this.messageService.error(
                    'Ocurrio un error desconocido en local'
                  );
            },
          });
      },
      reject: () => {
        this.confirmationService.close();
      },
    });
  }

  ngOnDestroy(): void {
    this.subs.forEach((sub) => sub.unsubscribe());
  }
}
